@import 'static/scss/variable.scss';
@import 'static/scss/mixins/mixins.scss';

.button-default {
  button {
    width: 150px;
    height: 45px;
    background: $blue-07;
    box-shadow: $box-shadow;
    border-radius: 5px;
    border: none;
    @include font(20px, $black);
    margin-bottom: 15px;
    &:hover {
      cursor: pointer;
      background: $blue-10;
    }
    &:active {
      background: $blue-20;
    }
  }
}