@import 'static/scss/variable.scss';
@import 'static/scss/mixins/mixins.scss';

.page-profile {
  .logout {
    display: flex;
    justify-content: flex-end;
  }
  .photo {
    margin-top: 100px;
    display: flex;
    justify-content: center;
  }
  .data-profile {
    margin: auto;
    justify-content: center;
    margin-top: 30px;
    width: 500px;
    @include font(16px, $black, 0.1em, 400);
    .item {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 15px;
    }
    .link-edit {
      margin-bottom: 15px;
    }
  }
}